<template>
  <div v-swiper:slider="options">
    <div class="swiper-wrapper">
      <div v-for="(img, index) in images" :key="index" class="swiper-slide">
        <div class="img__container" :style="{ 'background-image': `url(${img})` }"></div>
        <img :src="img" alt="">
      </div>
    </div>
  </div>
</template>

<script>
import { directive } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
// import 'swiper/swiper-bundle.css'

export default {
  name: 'ImageSlider',
  directives: {
    swiper: directive
  },
  props: {
    images: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      options: {
        speed: 1500,
        slidesPerView: 'auto'
      }
    }
  },
  methods: {
    prev () {
      this.slider.slidePrev()
    },
    next () {
      this.slider.slideNext()
    }
  }
}
</script>

<style lang="scss" scoped>
.swiper-container {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  background-color: #222;
}
.swiper-slide {
  width: 80%;
  > img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.img__container {
  position: absolute;
  top: 0;
  left: 0;
  width: 102%;
  height: 100%;
  background-color: #000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(10px);
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.35);
  }
}
</style>
