<template>
  <div>
    <div class="wrapper">
      <v-tabs-items class="h-100 black" v-model="tab">
        <v-tab-item reverse-transition="fade-transition" transition="fade-transition" class="h-100">
          <image-slider :images="salon" ref="salon"/>
        </v-tab-item>
        <v-tab-item reverse-transition="fade-transition" transition="fade-transition" class="h-100">
          <image-slider :images="decoracion" ref="decoracion"/>
        </v-tab-item>
        <v-tab-item reverse-transition="fade-transition" transition="fade-transition" class="h-100">
          <image-slider :images="jardin" ref="jardin"/>
        </v-tab-item>
      </v-tabs-items>
    </div>
    <div class="navigation px-8">
      <v-tabs
        background-color="transparent"
        color="#333e48"
        v-model="tab"
        height="40px"
        class="pt-4 pb-6"
      >
        <v-tab :ripple="false">
          SALN
        </v-tab>
        <v-tab :ripple="false">
          DECORACIN
        </v-tab>
        <v-tab :ripple="false">
          JARDN
        </v-tab>
      </v-tabs>
      <div class="icons">
        <svg-icon
          class="mx-4"
          name="chevron-left"
          color="#FFF"
          size="1.8em"
          @click.native="prev"
        />
        <svg-icon
          class="mx-4"
          name="chevron-right"
          color="#FFF"
          size="1.8em"
          @click.native="next"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ImageSlider from '@/components/sliders/ImageSlider'
import SvgIcon from '../components/ui/SvgIcon'

export default {
  name: 'Galeria',
  components: {
    ImageSlider,
    SvgIcon
  },
  data () {
    return {
      salon: [
        require('@/assets/img/servicios/01.jpg'),
        require('@/assets/img/salon/01.jpg'),
        require('@/assets/img/jardin/01.jpg')
      ],
      decoracion: [
        require('@/assets/img/salon/01.jpg'),
        require('@/assets/img/jardin/01.jpg'),
        require('@/assets/img/servicios/01.jpg')
      ],
      jardin: [
        require('@/assets/img/jardin/01.jpg'),
        require('@/assets/img/servicios/01.jpg'),
        require('@/assets/img/salon/01.jpg')
      ],
      tab: 0
    }
  },
  methods: {
    prev () {
      switch (this.tab) {
        case 0: this.$refs.salon.prev()
          break
        case 1: this.$refs.decoracion.prev()
          break
        case 2: this.$refs.jardin.prev()
          break
      }
    },
    next () {
      switch (this.tab) {
        case 0: this.$refs.salon.next()
          break
        case 1: this.$refs.decoracion.next()
          break
        case 2: this.$refs.jardin.next()
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  height: 90vh;
}
.navigation {
  background: $primary;
  border-bottom: 2px solid rgba(255, 255, 255, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
  .icons {
    display: flex;
    span {
      cursor: pointer;
    }
  }
}
</style>
